<template>
  <div>
    <!-- 移动端菜单切换按钮 -->
    <div class="mobile-toggle" @click="toggleMobileMenu">
      <el-icon size="24">
        <Expand v-if="!isMobileMenuVisible" />
        <Fold v-else />
      </el-icon>
    </div>

    <div class="side-navigation" 
      :class="{ 
        'side-navigation-dark': isDarkTheme,
        'mobile-visible': isMobileMenuVisible
      }"
    >
      <!-- 桌面端显示LOGO -->
      <div class="side-nav-header" v-if="!isMobile">
        <h3>
          <el-icon class="nav-logo"><Monitor /></el-icon>
          Ai Navigation
        </h3>
      </div>

      <div class="side-nav-content">
        <div class="side-nav-search">
          <el-input
            v-model="searchText"
            placeholder="搜索分类..."
            clearable
            prefix-icon="Search"
            @input="filterCategories"
          />
        </div>
        <el-menu
          :default-active="activeIndex"
          class="side-nav-menu"
          @select="handleSelect"
        >
          <el-menu-item 
            v-for="category in filteredCategories" 
            :key="category.name" 
            :index="category.name"
            :class="{ 'is-active': activeIndex === category.name }"
          >
            <el-icon>
              <component :is="category.icon || 'Folder'" />
            </el-icon>
            <span>{{ category.name }}</span>
            <span class="item-count">{{ category.links.length }}</span>
          </el-menu-item>
        </el-menu>
      </div>
    </div>
  </div>
</template>

<script>
import { Folder, Expand, Fold, Monitor, Search } from '@element-plus/icons-vue'
import { navData } from "../nav-data.js"
import * as ElementPlusIcons from '@element-plus/icons-vue'
import { nextTick } from 'vue'

export default {
  name: 'SideNavigation',
  components: {
    Folder,
    Expand,
    Fold,
    Monitor,
    Search,
    ...ElementPlusIcons
  },
  props: {
    isDarkTheme: {
      type: Boolean,
      default: false
    }
  },
  data() {
    return {
      activeIndex: '',
      categories: navData,
      isMobileMenuVisible: false,
      searchText: '',
      filteredCategories: navData,
      isMobile: window.innerWidth <= 768
    }
  },
  methods: {
    filterCategories() {
      if (!this.searchText) {
        this.filteredCategories = this.categories;
        return;
      }
      const searchLower = this.searchText.toLowerCase();
      this.filteredCategories = this.categories.filter(category => 
        category.name.toLowerCase().includes(searchLower)
      );
    },
    handleSelect(index) {
      this.activeIndex = index;
      this.$emit('category-selected', index);
      // 在移动端选择后自动隐藏菜单
      if (window.innerWidth <= 768) {
        this.isMobileMenuVisible = false;
      }
    },
    toggleMobileMenu() {
      this.isMobileMenuVisible = !this.isMobileMenuVisible;
      // 等待 DOM 更新后再触发 resize 事件
      nextTick(() => {
        window.dispatchEvent(new Event('resize'));
      });
    }
  },
  mounted() {
    // 创建一个防抖函数
    const debounce = (fn, delay) => {
      let timer = null;
      return function (...args) {
        if (timer) clearTimeout(timer);
        timer = setTimeout(() => fn.apply(this, args), delay);
      };
    };

    // 处理 resize 事件
    const handleResize = debounce(() => {
      if (window.innerWidth > 768 && !this.isMobileMenuVisible) {
        this.isMobileMenuVisible = false;
      }
      this.isMobile = window.innerWidth <= 768
    }, 100);

    window.addEventListener('resize', handleResize);

    // 保存引用以便清理
    this.handleResize = handleResize;
  },
  beforeUnmount() {
    // 清理事件监听
    if (this.handleResize) {
      window.removeEventListener('resize', this.handleResize);
    }
  }
}
</script>

